




import { Component, Prop, Vue } from 'vue-property-decorator'
import * as d3 from 'd3'
import * as d3Queue from 'd3-queue'
import * as topojson from 'topojson'
import tip from 'd3-tip'

@Component
export default class WorldMap extends Vue {
  @Prop()
  private msg!: string

  width = 3000/2.1
  height = 1250/2.1
  centered = false
  path
  svg
  g
  /*
  projection = d3
          .geoEquirectangular()
          .center([0,15])
          .scale(this.width/(2*Math.PI))
          .translate( [this.width / 2, this.height / 2])
  path = d3.geoPath().projection(this.projection)
  graticule = d3.geoGraticule()

  svg = d3
      .select(this.$el)
      .attr('width', this.width)
      .attr('height', this.height)

  g = this.svg.append('g') */

  async mounted () {
    this.width = 3000/2.1
    this.height = 1250/2.1

    const projection = d3
      .geoEquirectangular()
      .center([0, 15])
      .scale(this.width/(2*Math.PI))
      .translate([this.width / 2, this.height / 2])

    this.path = d3.geoPath().projection(projection)

    const graticule = d3.geoGraticule()

    this.svg = d3
      .select(this.$el)
      .attr('width', this.width)
      .attr('height', this.height)

    this.g = this.svg.append('g')

    const data:any = await d3.json(
      'https://unpkg.com/world-atlas@1.1.4/world/110m.json'
    )

    console.log(data)

    const features:any = topojson.feature(data, data.objects.countries)
    console.log(features.features)

    this.g.selectAll('path')
      .attr('class', 'countries')
      .data(features.features)
      .enter()
      .append('path')
      .attr('d', this.path)
      .style('pointer-events', 'visible')
      .style('fill', 'none')
      .style('stroke', 'white')
      .style('stroke-width', 0.5)
      .style('opacity', 0.8)
      .on('click', this.clicked)

    const nodes = [ { name: 'Zurich', lon: 8.5417, lat: 47.3769, available: 10, occupied: 3 },
      { name: 'Singapore', lon: 103.8198, lat: 1.3521, available: 4, occupied: 2 },
      { name: 'San Francisco', lon: -122.4194, lat: 37.7749, available: 44, occupied: 6 },
      { name: 'Sydney', lon: 151.2093, lat: -33.8688, available: 7, occupied: 8 },
      { name: 'Lagos', lon: 3.3792, lat: 6.5244, available: 7, occupied: 8 },
      { name: 'Buenos Aires', lon: -58.3816, lat: -34.6037, available: 2, occupied: 22 },
      { name: 'Shanghai', lon: 121.4737, lat: 31.2304, available: 9, occupied: 12 }]

    const tooltip = d3.select('#home')
      .append('div')
      .attr('class', 'tooltip')
      .style('opacity', 0)

    console.log(tooltip)

    const circles = this.g.selectAll('circle')
      .data(nodes)
      .enter()
      .append('circle')
      .attr('cx', d => { return projection([d.lon, d.lat])[0] })
      .attr('cy', d => { return projection([d.lon, d.lat])[1] })
      .attr('r', 5)
      .attr('fill', 'none')
      .on('mouseover', d => {
        tooltip
          .transition()
          .duration(200)
          .style('opacity', 0.9)
        tooltip
          .html('<h1>' + d.name + '</h1><br/><span>Total: ' + d.available + '</span>' + '<br/><span>Occupied: ' + d.occupied + '</span>')
          .style('left', d3.event.pageX + 15 + 'px')
          .style('top', d3.event.pageY - 28 + 'px')
      })
      .on('mouseout', () => {
        tooltip
          .transition()
          .duration(500)
          .style('opacity', 0)
      })

    circles
      .transition()
      .delay((d, i) => { return i * 500 })
      .on('start', function repeat () {
        d3.active(this)
          .style('stroke', 'rgb(255,255,25)')
          .style('stroke-width', 2)
          .style('fill', 'rgb(23,31,43)')
          .transition()
          .duration(1000)
          .transition()
          .attr('r', 10)
        /* .style('stroke', 'rgb(255,255,25)')
          .style('stroke-width', 2)
          .style('fill', 'rgb(234,96,1)') */
          .duration(1000)
          .transition()
          .attr('r', 5)
          .on('start', repeat)
      })
  }

  clicked (d) {
    var x, y, k
    console.log(d)
    if (d && this.centered !== d) {
      var centroid = this.path.centroid(d)
      x = centroid[0]
      y = centroid[1]
      k = 4
      this.centered = d
    } else {
      x = this.width / 2
      y = this.height / 2
      k = 1
      this.centered = null
    }

    this.g.selectAll('path')
      .classed('active', this.centered && function (d) { return d === this.centered })

    this.g.transition()
      .duration(750)
      .attr('transform', 'translate(' + this.width / 2 + ',' + this.height / 2 + ')scale(' + k + ')translate(' + -x + ',' + -y + ')')
      .style('stroke-width', 1.5 / k + 'px')
  }
}

